.Header {
  display: flex;
  justify-content: space-between;
  padding: 25px 40px;
  background-color: #DDBDD5;
}
.Header nav {
  width: 60%;
  display: flex;
  justify-content: space-around;
}
.Header .logoName {
  font-weight: 700;
  font-size: 1.5em;
  color: #4392E6;
}
.Header a {
  text-decoration: none;
  color: black;
  font-size: 1.3em;
}
.Header .cv-button {
  width: 5%;
  height: 40px;
  border-radius: 10px;
  font-size: 1em;
  background-color: #4392E6;
  color: aliceblue;
}
.Header .burger-menu {
  width: 50px;
  aspect-ratio: 1/1;
}/*# sourceMappingURL=Header.css.map */